<template>
    <div class="PasswordSetting">
      <HomePageTop :TopTitle="TopTotle" ></HomePageTop>
      <div class="fiexoverTitle mt24 border_E3E4E8">
        <div class="fiexover">
          <p>当前密码</p>
          <div class="f34 cl_333 lh102 h_100">
            <input type="text" class="bn text_right" placeholder="请输入"  v-model="PresentNumber" @input="judgeNumber">
<!--            <i class="iconfont"></i>-->
          </div>
        </div>
      </div>
      <div class="fiexoverTitle  border_E3E4E8">
        <div class="fiexover">
          <p>新密码</p>
          <div class="f34 cl_333 lh102 h_100">
            <input type="text" class="bn text_right" placeholder="请输入"  v-model="NewNumber" @input="judgeNumber">
<!--            <i class="iconfont"></i>-->
          </div>
        </div>
      </div>
      <div class="fiexoverTitle  border_E3E4E8">
        <div class="fiexover">
          <p>确认密码</p>
          <div class="f34 cl_333 lh102 h_100">
            <input type="text" class="bn text_right" placeholder="请输入"  v-model="affirmNumber" @input="judgeNumber">
<!--            <i class="iconfont"></i>-->
          </div>
        </div>
      </div>
      <div class="public-btn mt60  mr30 ml30" :class="SubmitClassbox==true?'public-btn':'public-btnNo'" @click="SubmitClassClick">
        确认修改
      </div>
    </div>

</template>

<script>
    import Vue from "vue"
    import { Toast } from 'vant';
    import HomePageTop from "../../common/HomePageTop";

    export default {
        name: "PasswordSetting",
        components: {HomePageTop},
        data() {
            return {
              //像子组件传值
              TopTotle:{name:"修改密码",left:true},
              //当前密码
              PresentNumber:"",
              //新密码
              NewNumber:"",
              //确认密码
              affirmNumber:"",
              //提交按钮颜色
              SubmitClassbox:false,
            }
        },
        methods: {
          //开启判断
          judgeNumber(){
            if(this.PresentNumber!=""&&this.NewNumber!=""&&this.affirmNumber!=""){
              this.SubmitClassbox=true
            }
          },
          //提交按钮
          SubmitClassClick(){
            if(this.SubmitClassbox==true){
              if(this.NewNumber==this.affirmNumber){
                const toast = Toast.loading({
                  duration: 0, // 持续展示 toast
                  forbidClick: true,
                  icon:require("../../../assets/img/common/succeed_f90.png"),
                  message: '修改成功',
                });
                let second = 2;
                const timer = setInterval(() => {
                  second--;
                  if (second) {
                  } else {
                    clearInterval(timer);
                    // 手动清除 Toast
                    Toast.clear();
                    this.$router.push({
                      path:"Setting",
                    })
                  }
                }, 1000);
              }else{
                Toast({
                  message: '请输入正确的密码',
                  icon:require("../../../assets/img/common/hint.png"),
                });
              }
            }else{
              Toast({
                message: '请输入正确的密码',
                icon:require("../../../assets/img/common/hint.png"),
              });
            }
          },
        },
        created() {

        }
    }

</script>

<style>
  .PasswordSetting .HomePageTop{
    background: #fff;
  }

</style>



